import React, { useEffect, useState } from "react";
import InputWork from "./inputWork";
import { updateWork } from "../../../config/api/UPDATE";
import { getSinggleDataWork } from "../../../config/api/GET";

const ModalUpdateWork = ({ works, uploaded, setUploaded, singgleIdWork }) => {
  const [data, setData] = useState();
  const [title, setTitle] = useState();
  const [link, setLink] = useState();
  const [desc, setDesc] = useState();
  const [image, setImage] = useState();
  const [msg, setMessage] = useState("");
  const [error, setError] = useState();

  let newValue = {
    title,
    link,
    desc,
  };

  useEffect(() => {
    getSinggleDataWork(singgleIdWork)
      .then((res) => setData(res.data))
      .catch((err) => {});
  }, [singgleIdWork, works]);

  const handleUpdate = async () => {
    try {
      await updateWork(singgleIdWork, newValue, image);
      setMessage("Karya Berhasil Diupload");
      setError(false);
      setUploaded(!uploaded);
    } catch (error) {
      setMessage(error.response?.data?.message);
      setError(true);
    }
  };

  return (
    <>
      <input type="checkbox" id="modal-update" className="modal-toggle" />
      <div className="modal">
        <div className="modal-box flex flex-col gap-4">
          {error ? (
            <p className="text-red-500 text-[10px] md:text-[12px]">{msg}</p>
          ) : (
            <p className="text-green-500 text-[10px] md:text-[12px]">{msg}</p>
          )}
          <InputWork
            label={"TITLE"}
            onChange={setTitle}
            placeholder={data && data.title}
          />
          <InputWork label={"LINK"} onChange={setLink} placeholder={data && data.link} />
          <InputWork label={"DESKRIPSI"} onChange={setDesc} placeholder={data && data.desc} />
          <div className="form-control w-full">
            <label className="label">
              <span className="label-text">THUMBNAIL</span>
            </label>
            <input
              required
              onChange={(e) => setImage(e.target.files[0])}
              type="file"
              className="file-input border-0 file-input-info w-full"
            />
            <label className="label">
              <span className="label-text-alt">Nb : Ukuran 16:9</span>
            </label>
          </div>
          <div className="modal-action">
            <button onClick={handleUpdate} className="btn btn-success">
              Update
            </button>
            <label
              htmlFor="modal-update"
              className="btn btn-error text-neutral-focus"
            >
              Close
            </label>
          </div>
        </div>
      </div>
    </>
  );
};

export default ModalUpdateWork;
