import axios from "axios";

export const logout = async () => {
  const res = await axios.delete("http://localhost:5000/api/logout");
  return res.data;
};

export const DeleteCollage = async(id) => {
  const res = await axios.delete(`http://localhost:5000/api/collage/${id}`);
  return res.data
}

export const DeleteWork = async (idWork, idCollage) => {
  const res = await axios.delete(
    `http://localhost:5000/api/work/${idWork}/collage/${idCollage}`
  );
  return res.data;
};
