import { useNavigate } from "react-router-dom";
import InputField from "../../component/input/inputfield";
import LabelField from "../../component/input/labelfield";
import axios from "axios";
import React, { useState } from "react";
import { createCollage } from "../../config/api/UPLOAD";

const RegisterCollageIndex = () => {
  const [username, setUsername] = useState("");
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [status, setStatus] = useState("Aktif");
  const [error, setError] = useState(false);
  const [message, setMessage] = useState();
  const navigate = useNavigate();

  const handleSubmit = async (e) => {
    e.preventDefault();
    try {
      await axios.post(
        "https://gold-gopher-yoke.cyclic.app/api/register/collage",
        {
          username,
          password,
          email,
          status,
        }
      );
      setError(false);
      setMessage("Berhasil Register");
      setTimeout(() => {
        navigate("/login");
      }, 2000);
    } catch (error) {
      setMessage(error.response.data.message);
      setError(true);
    }
  };

  return (
    <>
      <div className="w-full h-screen flex justify-center items-center bg-gradient-to-tr from-purple-400 via-pink-500 to-red-500">
        <form
          onSubmit={handleSubmit}
          className="bg-white w-[250px] md:w-[400px] flex flex-col gap-3 shadow-md rounded px-8 pt-6 pb-8 mb-4"
        >
          <p
            className={`${
              error ? "text-red-500" : "text-green-500"
            } text-[8px] md:text-[12px]`}
          >
            {message}
          </p>
          <div className="w-full flex flex-col gap-2">
            <LabelField labelName="Username" />
            <InputField
              placeholder="Username"
              type="text"
              onChange={(value) => setUsername(value)}
            />
          </div>
          <div className="w-full flex flex-col gap-2">
            <LabelField labelName="Email" />
            <InputField
              placeholder="example@gmail.com"
              type="text"
              onChange={(value) => setEmail(value)}
            />
          </div>
          <div className="w-full flex flex-col gap-2">
            <LabelField labelName="Password" />
            <InputField
              placeholder="***********"
              type="password"
              onChange={(value) => setPassword(value)}
              required={true}
            />
          </div>
          <select
            onChange={(e) => setStatus(e.target.value)}
            className="select select-md select-ghost w-full rounded"
          >
            <option value={"Aktif"}>Aktif</option>
            <option value={"Alumni"}>Alumni</option>
          </select>
          <div className="flex items-center justify-between">
            <button
              className="bg-blue-500 hover:bg-blue-700 text-white text-[12px] md:text-[18px] font-bold py-1 px-2 rounded focus:outline-none focus:shadow-outline"
              type="sumbit"
            >
              Register
            </button>
          </div>
        </form>
      </div>
    </>
  );
};

export default RegisterCollageIndex;
