import axios from "axios";

export const uploadWork = async (id, value, image) => {
  const res = await axios.post(
    `http://https://tired-ruby-chipmunk.cyclic.app/api/work/collage/${id}`,
    {
      ...value,
      image,
    },
    {
      headers: {
        "Content-Type": "multipart/form-data",
      },
    }
  );
  return res.data;
};

export const createCollage = async (value) => {
  await axios.post(
    `https://gold-gopher-yoke.cyclic.app/api/register/collage`,
    {
      ...value,
    }
  );
};