import React from "react";
import { faPenToSquare, faTrash } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { Link } from "react-router-dom";
import { DeleteWork } from "../../../config/api/DELETE";

const CardWork = ({ data, idCollage, setMsgDelete, setSingleIdWork }) => {
  const handleDelete = () => {
    const idWork = data._id;
    DeleteWork(idWork, idCollage);
    setMsgDelete("Karya Berhasil Dihapus");
  };

  const handleSingleDataWork = () => {
    setSingleIdWork(data._id)
  };

  return (
    <div className="bg-darkFocus rounded-xl w-full h-fit p-5 flex flex-col gap-5">
      <div className="aspect-video">
        <img
          className="rounded-xl aspect-video w-full h-full"
          src={`${
            data.thumbnail === "thumbnail"
              ? "https://images.unsplash.com/photo-1626495121472-477b55186c0b?ixlib=rb-4.0.3&ixid=MnwxMjA3fDB8MHxzZWFyY2h8Mnx8dGh1bWJuYWlsJTIwMTYlM0E5fGVufDB8MHwwfHw%3D&auto=format&fit=crop&w=500&q=60"
              : `http://localhost:5000/uploads/images/${data.thumbnail}`
          }`}
          alt=""
        />
      </div>
      <div className="flex gap-3 items-center justify-between">
        <div>
          <h2 className="card-title text-gray-200">{data.title}</h2>
          <p className="text-gray-500">{data.desc}</p>
        </div>
        <div className="flex gap-3">
          <label
            onClick={handleSingleDataWork}
            className="cursor-pointer"
            htmlFor="modal-update"
          >
            <FontAwesomeIcon
              className="text-xl cursor-pointer text-yellow-500 hover:text-yellow-600"
              icon={faPenToSquare}
            />
          </label>
          <FontAwesomeIcon
            onClick={handleDelete}
            className="text-xl cursor-pointer text-red-500 hover:text-red-600"
            icon={faTrash}
          />
        </div>
      </div>
    </div>
  );
};

export default CardWork;
