import React, { useEffect, useState } from "react";
import ModalUploadWork from "./component/modalUploadWork";
import CardWork from "./component/cardWork";
import ModalUpdateWork from "./component/modalUpdateWork";

const ListWork = ({ datas, uploaded, setUploaded, setMsgDelete }) => {
  const [works, setWorks] = useState([]);
  const [singgleIdWork, setSingleIdWork] = useState()

  useEffect(() => {
    setWorks(datas.workcollage);
  }, [datas, uploaded]);

  return (
    <>
      <section
        className={`${
          datas.role === "Mahasiswa" ? "block" : "hidden"
        } px-5 py-5 md:px-20`}
      >
        <label
          htmlFor="my-modal"
          className="btn btn-accent w-fit text-gray-200"
        >
          Upload Karyamu
        </label>
        <div className="px-5 py-5 grid grid-cols-1 md:grid-cols-2 gap-2 p-3">
          {works &&
            works.map((work, i) => (
              <CardWork
                key={i}
                data={work}
                idCollage={datas._id}
                setMsgDelete={setMsgDelete}
                setSingleIdWork={setSingleIdWork}
              />
            ))}
        </div>
      </section>
      <ModalUploadWork
        id={datas._id}
        uploaded={uploaded}
        setUploaded={setUploaded}
      />
      <ModalUpdateWork
      works={works}
        uploaded={uploaded}
        setUploaded={setUploaded}
        singgleIdWork={singgleIdWork}
      />
    </>
  );
};

export default ListWork;
