import React, { useEffect } from 'react'
import { Input } from "antd";

const InputChangeProfile = ({error, label, defaultValue, placeholder, onChange}) => {
  useEffect(() => {
    if(defaultValue) {
      onChange(defaultValue)
    }
  }, [defaultValue, onChange])

  const handleChange = (e) => {
    onChange(e.target.value)
  }

  return (
    <div className="form-control w-full">
      <label className="label">
        <span className="label-text">{label}</span>
      </label>
      <Input
        status={error ? "error" : ""}
        defaultValue={defaultValue}
        placeholder={placeholder}
        onChange={handleChange}
        className="bg-base-100 text-slate-200 py-2 placeholder:text-slate-600"
      />
      <label className="label">
        <span className="label-text text-[10px]">
          {/* Current username : {datas.username} */}
        </span>
      </label>
    </div>
  );
}

export default InputChangeProfile