import React, { useEffect, useState } from "react";
import { getCollage } from "../../config/api/GET";
import CardCollage from "./component/cardCollage";

const ListCollage = ({ datas, msgDeleteDasboard }) => {
  const [collages, setCollages] = useState([]);
  const [msgDelete, setMsgDelete] = useState("")

  useEffect(() => {
    getCollage(100).then((res) => setCollages(res.data));
    msgDeleteDasboard(msgDelete)
    setMsgDelete("")
  }, [msgDelete, msgDeleteDasboard]);

  return (
    <div
      className={`${
        datas.role === "Dosen" ? "block" : "hidden"
      } px-5 py-5 md:px-20 grid grid-cols-1 md:grid-cols-2 gap-2 p-3`}
    >
      {datas.role === "Dosen"
        ? collages
          && collages.map((collage, i) => {
              return <CardCollage key={i} data={collage} msgDelete={setMsgDelete} />;
            })
          : null}
    </div>
  );
};

export default ListCollage;
