import React, { useState } from "react";
import { Link, useNavigate } from "react-router-dom";
import InputField from "../../component/input/inputfield";
import LabelField from "../../component/input/labelfield";
import axios from "axios";
import { Button, message } from "antd";

const LoginIndex = () => {
  const [username, setUsername] = useState("");
  const [password, setPassword] = useState("");
  const [error, setError] = useState(false);
  const [msg, setMessage] = useState();
  const [messageApi, contextHolder] = message.useMessage();
  const key = "updatable";
  const navigate = useNavigate();

  const handleSubmit = async (e) => {
    e.preventDefault();
    try {
      const res = await axios.post(
        "https://gold-gopher-yoke.cyclic.app/api/login",
        {
          username,
          password,
        },
        {
          withCredentials: true
        }
      );
      console.log(res);
      setError(false);
      setMessage("Berhasil Login");
      setTimeout(() => {
        // navigate("/dashboard");
      }, 2000);
      messageApi.open({
        key,
        type: "loading",
        content: "Loading...",
      });
      setTimeout(() => {
        messageApi.open({
          key,
          type: "success",
          content: "Login Success",
          duration: 2,
        });
      }, 1000);
    } catch (error) {
      setMessage(error.response?.data.message);
      setError(true);
    }
  };

  return (
    <>
      {contextHolder}
      <div className="w-full h-screen flex justify-center items-center bg-gradient-to-tr from-purple-400 via-pink-500 to-red-500">
        <form
          onSubmit={handleSubmit}
          className="bg-white w-[250px] md:w-[400px] flex flex-col gap-3 shadow-md rounded px-8 pt-6 pb-8 mb-4"
        >
          <p
            className={`${
              error ? "text-red-500" : "text-green-500"
            } text-[8px] md:text-[12px]`}
          >
            {msg}
          </p>
          <div className="w-full flex flex-col gap-2">
            <LabelField labelName="Username" />
            <InputField
              placeholder="Username"
              type="text"
              onChange={(value) => setUsername(value)}
            />
          </div>
          <div className="w-full flex flex-col gap-2">
            <LabelField labelName="Password" />
            <InputField
              placeholder="***********"
              type="password"
              onChange={(value) => setPassword(value)}
            />
          </div>
          <div className="flex items-center justify-between">
            <Button
              onClick={handleSubmit}
              className="btn bg-blue-500 border-none hover:bg-blue-700 text-white text-[12px] md:text-[18px] font-bold py-1 px-2 rounded focus:outline-none focus:shadow-outline"
              type="sumbit"
            >
              Sign In
            </Button>
            <Link
            to={"/register-mahasiswa"}
              className="inline-block align-baseline font-bold text-[12px] md:text-[18px] text-blue-500 hover:text-blue-800"
              href="#"
            >
              Create Account?
            </Link>
          </div>
        </form>
      </div>
    </>
  );
};

export default LoginIndex;
