import React, { useEffect, useState } from "react";
import { useParams } from "react-router-dom";
import {
  getSinggleDataCollage,
  getSinggleDataLecture,
} from "../config/api/GET";
import ChangePassword from "../part/profil/changePassword";
import Changeprofil from "../part/profil/changeProfil";
import Header from "../part/profil/header";
import Hero from "../part/profil/hero";

const Profil = () => {
  const { id, role } = useParams();
  const [datas, setDatas] = useState([]);
  console.log(datas);

  useEffect(() => {
    if (role === "Dosen") {
      getSinggleDataLecture(id).then((res) => setDatas(res.data));
    } else if (role === "Mahasiswa") {
      getSinggleDataCollage(id).then((res) => setDatas(res.data));
    }
  }, [role, id]);

  return (
    <main>
      <Header username={datas?.username} />
      <Hero datas={datas} />
      <section className="grid grid-cols-1 md:grid-cols-2 px-5 md:px-20 gap-5 py-10">
        <Changeprofil datas={datas} setDatas={setDatas} />
        <ChangePassword />
      </section>
    </main>
  );
};

export default Profil;
