import React from 'react'
import { faRightFromBracket, faUser } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { Link } from "react-router-dom";

const Header = ({username}) => {
  return (
    <section>
      <div className="px-5 md:px-20 flex items-center justify-between w-full md:justify-start gap-3 py-10">
        <div className="flex w-full items-center gap-3">
          <div className="aspect-square p-3 w-14 flex justify-center items-center rounded-full bg-darkFocus">
            <FontAwesomeIcon
              className="aspect-square text-[18px]"
              icon={faUser}
            />
          </div>
          <p className="text-2xl text-slate-200">Profil {username}</p>
        </div>
        <Link to={"/dashboard"}>
          <FontAwesomeIcon
            className="cursor-pointer hover:text-red-500"
            icon={faRightFromBracket}
          />
        </Link>
      </div>
    </section>
  );
}

export default Header