import React, { useEffect, useState } from "react";
import { useParams } from "react-router-dom";
import { getSinggleDataCollage } from "../config/api/GET";
import Header from "../part/mhsUpdate/header";
import Hero from "../part/mhsUpdate/hero";

const MhsUpdate = () => {
  const { id } = useParams();
  const [data, setData] = useState();
  console.log(data);

  useEffect(() => {
    getSinggleDataCollage(id).then((res) => setData(res.data));
  }, [id]);

  return (
    <main>
      <Header username={data?.username} />
      <Hero data={data} />
    </main>
  );
};

export default MhsUpdate;
