import axios from "axios";

export const getToken = async() => {
  const res = await axios.get("https://gold-gopher-yoke.cyclic.app/api/token");
  return res.data
}

export const getSinggleDataLecture = async(id) => {
  const res = await axios.get(
    `https://gold-gopher-yoke.cyclic.app/api/lecture/${id}`
  );
  return res.data
}

export const getSinggleDataCollage = async(id) => {
  const res = await axios.get(
    `https://gold-gopher-yoke.cyclic.app/api/collage/${id}`
  );
  return res.data
}

export const getCollage = async(limit) => {
  const res = await axios.get(
    `https://gold-gopher-yoke.cyclic.app/api/collage?limit=${limit}`
  );
  return res.data
}

export const getSinggleDataWork = async (id) => {
  const res = await axios.get(
    `https://gold-gopher-yoke.cyclic.app/api/work/${id}`
  );
  return res.data;
};
