import React from "react";
import { BrowserRouter, Routes, Route, Navigate, HashRouter } from "react-router-dom";
import Dashboard from "./pages/dashboard";
import Login from "./pages/login";
import Profil from "./pages/profil";
import RegisterLecture from "./pages/registerLecture";
import MhsUpdate from "./pages/mhsUpdate";
import RegisterCollage from "./pages/registerCollage";

const App = () => {
  return (
    <HashRouter>
      <Routes>
        <Route path="/" element={<Navigate to="/dashboard" />} />
        <Route path="/dashboard" element={<Dashboard />} />
        <Route path="/login" element={<Login />} />
        <Route path="/register-dosen" element={<RegisterLecture />} />
        <Route path="/register-mahasiswa" element={<RegisterCollage />} />
        <Route path="/profil/:role/:id" element={<Profil />} />
        <Route path="/update/mahasiswa/:id" element={<MhsUpdate />} />
      </Routes>
    </HashRouter>
  );
};

export default App;
