import React, { useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";
import jwt_decode from "jwt-decode";
import { getSinggleDataCollage, getSinggleDataLecture, getToken } from "../config/api/GET";
import Navbar from "../component/navbar/navbar";
import WelcomUser from "../part/dashboard/welcomUser";
import Status from "../part/dashboard/status";
import ListCollage from "../part/dashboard/listCollage";
import Footer from "../component/footer/footer";
import ListWork from "../part/dashboard/listWork";

const Dashboard = () => {
  const navigate = useNavigate();
  const [isAuthorized, setIsAuthorized] = useState(false);
  const [token, setToken] = useState("");
  const [tokenDecoded, setTokenDecoded] = useState();
  const [datas, setDatas] = useState([]);
  const [msgDelete, setMsgDelete] = useState("")
  const [uploaded, setUploaded] = useState(false)

  useEffect(() => {
    getToken()
      .then((res) => {
        console.log(res);
        const decoded = jwt_decode(res.accessToken);
        setToken(res.accessToken);
        setTokenDecoded(decoded);
        setIsAuthorized(true);
      })
      .catch((error) => {
        console.log(error);
        setIsAuthorized(false);
        navigate("/login");
      });
  }, [navigate, isAuthorized, token]);

  useEffect(() => {
    if (tokenDecoded) {
      const id = tokenDecoded?.id;
      const role = tokenDecoded.role;
      if (role === "Dosen") {
        getSinggleDataLecture(id).then((res) => setDatas(res.data));
      }else if(role === "Mahasiswa"){
        getSinggleDataCollage(id).then((res) => setDatas(res.data));
      }
    }
  }, [tokenDecoded, uploaded, msgDelete]);

  useEffect(() => {
    setMsgDelete("")
  }, [msgDelete])

  return (
    <>
      <header>
        <Navbar datas={datas} />
      </header>
      <main className="py-5">
        <WelcomUser datas={datas.username} />
        <Status datas={datas} msgDelete={msgDelete} />
        <ListCollage datas={datas} msgDeleteDasboard={setMsgDelete} />
        <ListWork
          datas={datas}
          uploaded={uploaded}
          setUploaded={setUploaded}
          setMsgDelete={setMsgDelete}
        />
      </main>
      <Footer />
    </>
  );
};

export default Dashboard;
