import React from 'react'
import { Input } from "antd";
import { useState } from 'react';

const ChangePassword = () => {
    const [message, setMessage] = useState();
    const [error, setError] = useState();

  return (
    <div className="w-full flex flex-col p-5 gap-3 bg-darkFocus rounded-xl">
      <p
        className={`${
          error ? "text-red-500" : "text-green-500"
        } text-[10px] md:text-[12px]`}
      >
        {/* {message} */}
        Coming Soon
      </p>
      <div className="form-control w-full">
        <label className="label">
          <span className="label-text">Current password</span>
        </label>
        <Input
          status={error ? "error" : ""}
          className="bg-base-100 text-slate-200 py-2 placeholder:text-slate-600"
        />
        <label className="label">
          <span className="label-text text-[10px]">
            Required. Your current password
          </span>
        </label>
      </div>
      <div className="form-control w-full">
        <label className="label">
          <span className="label-text">New password</span>
        </label>
        <Input
        type='password'
          status={error ? "error" : ""}
          className="bg-base-100 text-slate-200 py-2 placeholder:text-slate-600"
        />
        <label className="label">
          <span className="label-text text-[10px]">Required. New password</span>
        </label>
      </div>
      <div className="form-control w-full">
        <label className="label">
          <span className="label-text">Confirm password</span>
        </label>
        <Input className="bg-base-100 text-slate-200 py-2 placeholder:text-slate-600" />
        <label className="label">
          <span className="label-text text-[10px]">
            Required. New password one more time
          </span>
        </label>
      </div>
      <button className="btn btn-sm btn-info w-[30%] mt-3 text-sm">
        Update
      </button>
    </div>
  );
}

export default ChangePassword