import {
  faChalkboardUser,
  faGraduationCap,
  faMars,
  faVenus,
} from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import React from "react";

const Hero = ({ datas }) => {
  return (
    <section className="w-full h-fit px-5 md:px-20 flex justify-center">
      <div className="bg-darkFocus flex flex-col md:flex-row justify-center items-center py-4 w-full h-full rounded-xl">
        <div className="aspect-square h-[200px] md:h-[300px]">
          <img
            className="aspect-square rounded-full shadow"
            src={`${
              datas.detailuser?.thumbnail
                ? `http://localhost:5000/uploads/images/${datas?.detailuser?.thumbnail}`
                : "https://images.unsplash.com/photo-1529665253569-6d01c0eaf7b6?ixlib=rb-4.0.3&ixid=MnwxMjA3fDB8MHxzZWFyY2h8NHx8cHJvZmlsZXxlbnwwfHwwfHw%3D&auto=format&fit=crop&w=500&q=60"
            }`}
            alt=""
          />
        </div>
        <div className="flex flex-col justify-center items-center md:items-start gap-3 px-10 py-5">
          <h1 className="text-2xl text-slate-200">
            Hallo,{" "}
            <span className="text-slate-100 text-3xl font-semibold">
              {datas?.username}!
            </span>
          </h1>
          <div className="flex gap-3 items-center text-md">
            <h1>{datas?.detailuser?.fullname}</h1>
            <FontAwesomeIcon
              className={`${
                datas?.detailuser?.gender === "Laki - Laki"
                  ? "text-blue-500"
                  : "text-pink-500"
              }`}
              icon={
                datas?.detailuser?.gender === "Laki - Laki" ? faMars : faVenus
              }
            />
          </div>
          <div className="w-fit bg-blue-500 rounded-full flex justify-center items-center gap-2 py-1 px-5">
            <FontAwesomeIcon
              className={`${
                datas?.role === "Dosen"
                  ? "text-yellow-500"
                  : `${
                      datas?.status === "Aktif"
                        ? "text-green-500"
                        : "text-red-500"
                    }`
              }`}
              icon={
                datas?.role === "Dosen" ? faChalkboardUser : faGraduationCap
              }
            />
            <p className="text-slate-200 text-sm font-bold">
              {datas?.role === "Dosen"
                ? "Dosen"
                : `Mahasiswa | ${
                    datas?.status === "Aktif" ? "Aktif" : "Alumni"
                  }`}
            </p>
          </div>
        </div>
      </div>
    </section>
  );
};

export default Hero;
