import React from "react";
import ProfilMhs from "./component/profilMhs";
import { Input } from "antd";

const Hero = ({ data }) => {
  return (
    <section className="w-full h-fit px-5 md:px-20">
      <div className="grid justify-center bg-darkFocus rounded-xl">
        <ProfilMhs data={data} />
      </div>
      <div>
        Karya Mahasiswa
      </div>
    </section>
  );
};

export default Hero;
