import axios from "axios";

export const updateLecture = async(id, value, image) => {
  const res = await axios.put(
    `http://localhost:5000/api/lecture/${id}`,
    {
      ...value,
      image,
    },
    {
      headers: {
        "Content-Type": "multipart/form-data",
      },
    }
  );
  return res.data
}

