import { faHand } from '@fortawesome/free-solid-svg-icons'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import React from 'react'

const WelcomUser = ({datas}) => {
  return (
    <div className='px-20 flex items-center justify-center md:justify-start gap-3 mb-3 pt-20'>
      <div className='hidden md:block aspect-square p-3 w-9 md:flex justify-center items-center rounded-full bg-darkFocus'>
        <FontAwesomeIcon className='aspect-square text-[12px]' icon={faHand} />
      </div>
      <p className='text-md'>Welcom {datas}</p>
    </div>
  )
}

export default WelcomUser