import { faChalkboardUser, faGraduationCap,  faUserGroup } from '@fortawesome/free-solid-svg-icons'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import React, { useEffect, useState } from 'react'
import { getCollage } from '../../config/api/GET'

const Status = ({datas, msgDelete}) => {
  const [collage, setCollage] = useState([])

  useEffect(() => {
    getCollage(100).then( res => setCollage(res.data))
  }, [msgDelete])

  const aktif = collage.filter(item => item.status === "Aktif")
  const alumni = collage.filter((item) => item.status === "Alumni");

  return (
    <div className={`${datas.role === "Dosen" ? "block" : "hidden"} px-5 md:px-20 w-full `}>
      <div className='grid grid-cols-1 md:grid-cols-3 w-full gap-4'>
        <div className='w-full h-[100px] flex justify-between md:justify-around items-center rounded-xl bg-darkFocus p-3'>
          <div className='leading-3'>
            <h1 className='text-slate-400'>Mahasiswa</h1>
            <p className='text-3xl text-slate-200 font-bold'>{collage.length}</p>
          </div>
          <FontAwesomeIcon className='text-emerald-500 text-3xl' icon={faUserGroup} />
        </div>
        <div className='w-full h-[100px] flex justify-between md:justify-around items-center rounded-xl bg-darkFocus p-3'>
          <div className='leading-3'>
            <h1 className='text-slate-400'>Aktif</h1>
            <p className='text-3xl text-slate-200 font-bold'>{aktif.length}</p>
          </div>
          <FontAwesomeIcon className='text-blue-500 text-3xl' icon={faChalkboardUser} />
        </div>
        <div className='w-full h-[100px] flex justify-between md:justify-around items-center rounded-xl bg-darkFocus p-3'>
          <div className='leading-3'>
            <h1 className='text-slate-400'>Alumni</h1>
            <p className='text-3xl text-slate-200 font-bold'>{alumni.length}</p>
          </div>
          <FontAwesomeIcon className='text-red-500 text-3xl' icon={faGraduationCap} />
        </div>
      </div>
    </div>
  )
}

export default Status