import React from 'react'
import { faPenToSquare, faTrash } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { Link } from 'react-router-dom';
import { DeleteCollage } from '../../../config/api/DELETE';

const CardCollage = ({data, msgDelete}) => {

  const handleDelete = () => {
    DeleteCollage(data._id).then(res => msgDelete(res.data?.message))
  }

  return (
    <div className="bg-darkFocus rounded-xl w-full h-fit p-5 flex items-center justify-between">
      <div className="flex">
        <div>
          <img
            className="aspect-square w-[40px] rounded-full shadow"
            src={`${
              data.detailuser?.thumbnail
                ? `http://localhost:5000/uploads/images/${data.detailuser?.thumbnail}`
                : "https://images.unsplash.com/photo-1529665253569-6d01c0eaf7b6?ixlib=rb-4.0.3&ixid=MnwxMjA3fDB8MHxzZWFyY2h8NHx8cHJvZmlsZXxlbnwwfHwwfHw%3D&auto=format&fit=crop&w=500&q=60"
            }`}
            alt=""
          />
        </div>
        <div className="px-5">
          <h1 className="font-roboto text-[18px] text-slate-300">
            {data.detailuser?.fullname
              ? data.detailuser?.fullname
              : data.username}
          </h1>
          <p className="text-[14px] text-slate-500">{data.status}</p>
        </div>
      </div>
      <div className="flex gap-3">
        <Link to={`/update/mahasiswa/${data._id}`} className="cursor-pointer">
          <FontAwesomeIcon
            className="text-xl cursor-pointer text-yellow-500 hover:text-yellow-600"
            icon={faPenToSquare}
          />
        </Link>
        <div onClick={handleDelete}>
          <FontAwesomeIcon
            className="text-xl cursor-pointer text-red-500 hover:text-red-600"
            icon={faTrash}
          />
        </div>
      </div>
    </div>
  );
}

export default CardCollage