import React from 'react'
import {
  faChalkboardUser,
  faGraduationCap,
  faMars,
  faVenus,
} from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";

const ProfilMhs = ({data}) => {
  return (
    <div className="flex flex-col justify-center items-center py-4 w-full h-full">
      <div className="aspect-square h-[200px] md:h-[300px]">
        <img
          className="aspect-square rounded-full shadow"
          src={`${
            data?.detailuser?.thumbnail
              ? `http://localhost:5000/uploads/images/${data?.detailuser?.thumbnail}`
              : "https://images.unsplash.com/photo-1494790108377-be9c29b29330?ixlib=rb-4.0.3&ixid=MnwxMjA3fDB8MHxzZWFyY2h8M3x8cHJvZmlsZXxlbnwwfHwwfHw%3D&auto=format&fit=crop&w=500&q=60"
          }`}
          alt=""
        />
      </div>
      <div className="flex flex-col justify-center items-center gap-1 px-10 py-5">
        <h1 className="text-2xl text-slate-200 text-start md:text-center">
          Hallo,{" "}
          <span className="text-slate-100 text-3xl font-semibold">
            {data?.username}!
          </span>
        </h1>
        <div className="flex gap-3 items-center text-md">
          <h1>{data?.detailuser?.fullname}</h1>
          <FontAwesomeIcon
            className={`${
              data?.detailuser?.gender === "Laki - Laki"
                ? "text-blue-500"
                : "text-pink-500"
            }`}
            icon={data?.detailuser?.gender === "Laki - Laki" ? faMars : faVenus}
          />
        </div>
        <div className="w-fit bg-blue-500 rounded-full flex justify-center items-center gap-2 py-1 px-5">
          <FontAwesomeIcon
            className={`${
              data?.role === "Dosen"
                ? "text-yellow-500"
                : `${
                    data?.status === "Aktif" ? "text-green-500" : "text-red-500"
                  }`
            }`}
            icon={data?.role === "Dosen" ? faChalkboardUser : faGraduationCap}
          />
          <p className="text-slate-200 text-sm font-bold">
            {data?.role === "Dosen"
              ? "Dosen"
              : `Mahasiswa | ${data?.status === "Aktif" ? "Aktif" : "Alumni"}`}
          </p>
        </div>
      </div>
    </div>
  );
}

export default ProfilMhs