import React, { useEffect, useState } from "react";
import { updateCollage, updateLecture } from "../../config/api/UPDATE";
import InputChangeProfile from "./component/inputChangeProfil";

const Changeprofil = ({ datas, setDatas }) => {
  const [username, setUsername] = useState("");
  const [fullname, setFullname] = useState("");
  const [email, setEmail] = useState("");
  const [gender, setgender] = useState("Laki - Laki");
  const [image, setimage] = useState();
  const [msg, setMessage] = useState("");
  const [error, setError] = useState();


  let newValue = {
    username,
    fullname,
    email,
    gender,
  };

  const handleUpdate = async () => {
    try {
      switch (datas?.role) {
        case "Dosen":
          const resLecture = await updateLecture(datas._id, newValue, image);
          setDatas(resLecture.data);
          break;

        case "Mahasiswa":
          const resCollage = await updateCollage(datas._id, newValue, image)
          setDatas(resCollage.data)
          break

        default:
          break;
      }
      setMessage("Data berhasil diupdate");
      setError(false);
    } catch (error) {
      setMessage(error?.response?.data?.message);
      setError(true);
    }
  };



  return (
    <div className="flex flex-col items-center w-full gap-5">
      <div className="w-full h-fit flex flex-col p-5 bg-darkFocus rounded-xl">
        <div className="form-control w-full max-w-xs">
          <label className="label">
            <span className="label-text text-md text-slate-200">Avatar</span>
          </label>
          <input
            onChange={(e) => setimage(e.target.files[0])}
            type="file"
            className="file-input file-input-info border-0 w-full max-w-xs"
          />
          <label className="label">
            <span className="label-text-alt text-[9px]">Max 1Mb</span>
          </label>
        </div>
      </div>
      <div className="w-full flex flex-col p-5 gap-3 bg-darkFocus rounded-xl">
        {error ? (
          <p className="text-red-500 text-[10px] md:text-[12px]">{msg}</p>
        ) : (
          <p className="text-green-500 text-[10px] md:text-[12px]">{msg}</p>
        )}
        <InputChangeProfile
          error={error}
          label="Username"
          defaultValue={datas?.username}
          placeholder={datas?.username}
          onChange={setUsername}
        />
        <InputChangeProfile
          error={error}
          label="Fullname"
          defaultValue={datas?.detailuser?.fullname}
          placeholder={datas?.detailuser?.fullname}
          onChange={setFullname}
        />
        <InputChangeProfile
          error={error}
          label="Email"
          defaultValue={datas?.email}
          placeholder={datas?.email}
          onChange={setEmail}
        />
        <div className="flex flex-col gap-3">
          <div className="form-control w-32">
            <label className="label p-0 cursor-pointer">
              <span className="label-text">Laki - Laki</span>
              <input
                defaultChecked
                type="radio"
                name="radio-10"
                className="radio radio-sm radio-info"
                onChange={() => setgender("Laki - Laki")}
              />
            </label>
          </div>
          <div className="form-control w-32">
            <label className="label p-0 cursor-pointer">
              <span className="label-text">Perempuan</span>
              <input
                value="Perempuan"
                type="radio"
                name="radio-10"
                className="radio radio-sm radio-secondary"
                onChange={() => setgender("Perempuan")}
              />
            </label>
          </div>
        </div>
        <button
          onClick={handleUpdate}
          className="btn btn-sm btn-info w-[30%] mt-3 text-sm"
        >
          Update
        </button>
      </div>
    </div>
  );
};

export default Changeprofil;
